@font-face {
  font-family: Texturina;
  src: url("./fonts/Texturina.ttf")
}

@font-face {
  font-family: Ubuntu;
  src: url("./fonts/Ubuntu.ttf")
}

*,
*::after,
*::before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: auto;
}

h1,
span {font-family: Ubuntu, serif;}
h2, p {
    font-family: Texturina, sans-serif;}

/*------------------------------------------------------------------------------------*/

header,
footer {
  background-color: #333;
  color: #fff;
  padding: 15px 0;
}

header .container,
footer .container {
  display: flex;
  justify-content: center;
}

header h1 {
  font-size: 2.5em;
  letter-spacing: 3px;
}
header svg {
  width: 40px;
  padding-left: 5px;
}

/*------------------------------------------------------------------------------------*/

#search-box {
  max-width: 900px;
  margin: auto;
  padding: 30px 30px 0;
}

#search-box input {
  width: 100%;
  font-size: 18px;
  padding: 8px 16px;
  background-color: #eaf8ff;
  border: 2px solid #00a1ff;
  outline: none;
  border-radius: 5px;
}

#search-box input:focus {
  border-width: 3px;
}

/*------------------------------------------------------------------------------------*/

#questions {
  padding: 40px 0 20px;
  min-height: calc(100vh - 200px);
}

#questions .container {
  display: flex;
  flex-direction: column;
  align-content: center;

  max-width: 1000px;
}

.hidden{
    display: none;
}
.question-box {
  background-color: #f1f1f1;
  padding: 20px;
  margin-bottom: 20px;

  border-left: 5px solid #00a1ff;
  border-radius: 10px;
}

.question-box .question {
  color: #00a1ff;
  font-size: 20px;

  padding-bottom: 5px;
}

.question-box div {
  padding: 5px;
  cursor: pointer;
}

.question-box div input::before {
  content: '';
  display: inline-block;
  position: relative;
  top: -1px;
  left: -2px;

  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 3px solid #fff;
  background-color: #e8e8e8;
  cursor: pointer;
}

.question-box div:hover input::before {
  background-color: #00a1ff;
}

.question-box span {
  font-weight: 700;
}

.question-bad{
  border-left-color: #f00;
}
.question-good{
  border-left-color: #03c603;
}

.color-input::before {background-color: #00a1ff !important;}

.finish::before {background-color: #e8e8e8 !important;}

.color-input::before,
.finish::before {cursor: auto !important;}

.selected::before {background-color: #00a1ff !important;}

.correct::before {background-color: #03c603 !important;}
/*------------------------------------------------------------------------------------*/

footer p {font-size: 20px;}
footer svg {
  width: 25px;
  padding-left: 5px;
  position: relative;
  bottom: -3px;
}